import { ApiProvider } from './../api/api';
import { Injectable } from '@angular/core';

@Injectable()
export class NoticeProvider {

  constructor( public api: ApiProvider ) {}

  /**
   * busca todas as noticias
   * 
   * @param page 
   */
  public getNotices( page = 1, categories = [] ) {

    const categories_ids = categories.map( item => item.id );

    return this.api.post( `/notice/get_notices/${page}`, { categories_ids } )
    .then( notices => notices )
    .catch( err => err );
  }

  /**
   * Salva a noticia para ler depois
   * 
   * @param notice_id 
   */
  public saveForLater( notice_id ) {
    return this.api.get( `/notice/save_for_later/${notice_id}` )
    .then( success => success )
    .catch( err => err );
  }

  /**
   * Desmarca o ver depois
   */
  public unsaveForLater( notice_id ) {
    return this.api.get( `/notice/unsave_for_later/${notice_id}` )
    .then( success => success )
    .catch( err => err );
  }

  /**
   * Pega as noticias salvas
   * 
   * @param page 
   */
  public getSavedNotices( page = 1 ) {
    return this.api.get( `/notice/get_saved_notices/${page}` )
    .then( notices => notices )
    .catch( err => err );
  }

  /**
   * Pega as noticias do veiculo
   */
  public getGatewayNotices( gateway_id, page = 1 ) {
    return this.api.get( `/notice/get_gateway_notices/${gateway_id}/${page}` )
    .then( notices => notices )
    .catch( err => err );
  }

  /**
   * Denuncia uma noticia
   */
  public report( notice_id ) {
    return this.api.get( `/notice/report/${notice_id}` )
    .then( success => success )
    .catch( err => err );
  }

  /**
   * Faz a busca de uma noticia pelo titulo
   */
  public searchNotice( string, page = 1 ) {
    return this.api.get( `/notice/search_notice/${string}/${page}` )
    .then( notices => notices )
    .catch( err => err );
  }

  /**
   * Busca uma noticia pelo id
   * @param notice_id 
   */
  public getById( notice_id ) {
    return this.api.get( `/notice/get/${notice_id}` )
    .then( notice => notice )
    .catch( err => err );
  }
}



