import { Region } from './../../providers/region/region.model';
import { Pipe, PipeTransform } from '@angular/core';

/**
 * Generated class for the TranslatePipe pipe.
 *
 * See https://angular.io/api/core/Pipe for more info on Angular Pipes.
 */
@Pipe({
  name: 'translate',
  pure: false
})
export class TranslatePipe implements PipeTransform {

  /**
   * Ultima regiao filtrada
   *
   */
  lastRegion = null;

  /**
   * Pega a listagem das regioes salvas
   * 
   */
  listFromStorage(): Region[] {
    
    // Pega no localstorage
    const regions = localStorage.getItem( 'synced-regions' );

    // Volta a listagem
    return regions ? JSON.parse( regions ) as Region[] : [];
  }

  /**
   * Pega a regiao ativa
   * 
   */
  getActivedRegion(): Region {

    // Pega todas as regioes
    const reg = this.listFromStorage();
    if (!reg) return null;

    // Verifica se existe algum ativo
    for (let r in reg) {
      if (reg[r].actived) return reg[r];
    }
  }

  /**
   * Takes a value and makes it lowercase.
   */
  transform(value: string, ...args) {

    // Obtem o regiao ativa
    const region = this.getActivedRegion();
    if (!region) return value;
    if ( this.lastRegion && this.lastRegion == region.id ) return value;
    this.lastRegion = region.id;

    // Obtem as configuracoes
    const config = localStorage.getItem( 'translate' );
    if ( !config ) return value;

    // Seta o objeto
    const obj = JSON.parse( config );
    if ( obj[region.sigla] ) {
      const words = obj[region.sigla];
      const word = words.find( elem => {
        return ( elem.key === value );
      });
      if ( word ) return word.val;
    }

    return value;
  }
}

