import { Events } from 'ionic-angular';
import { ApiProvider } from './../api/api';
import { Injectable } from '@angular/core';

@Injectable()
export class AuthProvider {

  /**
   * Método construtor
   * @param api 
   */
  public constructor( public api: ApiProvider, public events: Events ) {}

  /**
   * Faz o logout
   *
   */
  public logout() {
    localStorage.removeItem( 'auth-id' );
    localStorage.removeItem( 'auth-name' );
    localStorage.removeItem( 'auth-email' );
    localStorage.removeItem( 'auth-token' );
    localStorage.removeItem( 'feed-id' );
    this.events.publish( 'logout' );
  }

  /**
   * user
   * 
   * busca o usuario no localstorage
   */
  public user() {
    let user = localStorage.getItem( 'auth-id' );
    if ( user ) {
      return {
        name: localStorage.getItem( 'auth-name' ),
        email: localStorage.getItem( 'auth-email'),
        id: localStorage.getItem( 'auth-id' ),
        token: localStorage.getItem( 'auth-token' )
      };
    } else return null;
  }

  /**
   * signup
   * 
   * faz o cadastro do usuario
   * 
   * @param dados 
   */
  public signup( dados ) {

    // retorna a promessa
    return new Promise( ( resolve, reject ) => {

      this.api.post( '/auth/signup', { name     : dados['name'], 
                                      email    : dados['email'],
                                      password : dados['password'],
                                      confirm  : dados['confirm']  } )
      .then( user => {
        
        // seta o nome no local storage
        localStorage.setItem( 'auth-name', user['name'] );

        // seta o email no local storage
        localStorage.setItem( 'auth-email', user['email'] );

        // seta o id no local storage
        localStorage.setItem( 'auth-id', user['id'] );

        // retorna o usuario
        resolve( this.user );
      }).catch( err => reject( err ) );     
    });
  }

  /**
   * login
   * 
   * realiza o login do usuario
   * 
   * @param dados
   */
  public login( dados ) {

    // retorna a promessa
    return new Promise( ( resolve, reject ) => {

      // busca os estados na api
      this.api.post( '/auth/login/', { email : dados['email'], password : dados['password'] } )
      .then( user => {

        // seta o nome no local storage
        localStorage.setItem( 'auth-name', user['name'] );

        // seta o email no local storage
        localStorage.setItem( 'auth-email', user['email'] );

        // seta o token no local storage
        localStorage.setItem( 'auth-token', user['token_api'] );

        // seta o id no local storage
        localStorage.setItem( 'auth-id', user['id'] );

        // Dispara o evento
        this.events.publish( 'login' );
        
        // retorna o usuario
        resolve( this.user );
      } ).catch( err => reject( err ) );
    });
  }

  /**
   * Reseta a senha
   * @param dados 
   */
  public forgotPassword( dados ) {
    
    // retorna a promessa
    return new Promise( ( resolve, reject ) => {
      
      // busca os estados na api
      this.api.post( '/auth/forgot_password/', { email : dados['email'] } )
      .then( successo => resolve( successo ) )
      .catch( err => reject( err ) );
    });
  }

  /**
   * Atualiza os dados do usuario
   * @param dados 
   */
  public updateProfile( dados ) {

    return this.api.post( '/auth/update_profile/', { name     : ( dados['name'] ) ? dados['name'] : '',
                                                     email    : ( dados['email'] ) ? dados['email'] : '',
                                                     password : ( dados['password'] ) ? dados['password'] : '',
                                                     image    : ( dados['image'] ) ? dados['image'] : '' } )
    .then( user => {
      // seta o nome no local storage
      localStorage.setItem( 'auth-name', user['name'] );
      
      // seta o email no local storage
      localStorage.setItem( 'auth-email', user['email'] );
      
      // seta o token no local storage
      localStorage.setItem( 'auth-token', user['token_api'] );
      
      // seta o id no local storage
      localStorage.setItem( 'auth-id', user['id'] );

      return this.user();
    })
    .catch( err => err );
  }

  /**
   * Busca aimagem do usuario
   */
  public getUserImage() {
    return this.api.get( '/auth/get_user_image' )
    .then( image => image )
    .catch( err => err );
  }
}

