import { ApiProvider } from './../api/api';
import { Injectable } from '@angular/core';

@Injectable()
export class GatewayProvider {

  constructor( public api: ApiProvider ) {}

  /**
   * getGatewayById
   * 
   * @param id 
   */
  public get( id ) {

    // busca as noticias na api
    return this.api.get( `/gateway/get/${id}` )
    .then( gateway => gateway )
    .catch( err => err );
  }

  /**
   * saveFollow
   * 
   * salva o follow
   * 
   * @param dados 
   */
  public follow( gateway_id, personalCapegoryId = false ) {
    return this.api.get( `/gateway/follow/${gateway_id}/${personalCapegoryId}` )
    .then( success => success )
    .catch( err => err );
  }

  /**
   * Deixa de seguir um veiculo
   * 
   * @param dados 
   */
  public unfollow( gateway_id ) {
    return this.api.get( `/gateway/unfollow/${gateway_id}` )
    .then( success => success )
    .catch( err => err );
  }

  /**
   * Deica de seguir varios veiculos
   * 
   */
  public unfollowMany( gateways_id ) {

    return this.api.post( '/gateway/unfollow_many', { gateways_id } )
    .then( success => success )
    .catch( err => err );
  }

  /**
   * Silencia um veiculo
   * 
   * @param dados 
   */
  public mute( gateway_id ) {
    return this.api.get( `/gateway/mute/${gateway_id}` )
    .then( success => success )
    .catch( err => err );
  }

  /**
   * Silencia um veiculo
   * 
   * @param dados 
   */
  public unmute( gateway_id ) {
    return this.api.get( `/gateway/unmute/${gateway_id}` )
    .then( success => success )
    .catch( err => err );
  }

  /**
   * Denuncia um veiculo
   * 
   * @param gateway_id 
   */
  public report( gateway_id ) {
    return this.api.get( `/gateway/report/${gateway_id}` )
    .then( success => success )
    .catch( err => err );
  }

  /**
   * Busca a inscrições pela categoria
   * @param category_id 
   */
  public getSubscriptionByCategory( category_id ) {
    return this.api.get( `/gateway/get_subscription_by_category/${category_id}` )
    .then( subscriptions => subscriptions )
    .catch( err => err );
  }

  /**
   * Obtem os gateways por categoria
   * 
   * @param category_id 
   * @param page 
   */
  public getGatewayByCategory( query = '', category_id, page = 1 ) {
    const q   = encodeURI( query );
    const uri = `/gateway/get_by_category/${category_id}/${page}?query=${q}`;
    return this.api.get( uri )
    .then( gateways => gateways )
    .catch( err => err );
  }

  /**
   * Busca as inscrições ordenadas
   */
  public getOrderedSubscriptions() {
    return this.api.get( '/gateway/get_ordered_subscriptions' )
    .then( subscriptions => subscriptions )
    .catch( err => err );
  }

  /**
   * Busca o gateway pela query
   * @param query 
   */
  public searchAll( query = '', page = 1 ) {
    const q   = encodeURI( query );
    const uri = `/gateway/search_all/${page}?query=${q}`;
    return this.api.get( uri )
    .then( gateways => gateways )
    .catch( err => err );
  }
}

