import { SettingsProvider } from './../providers/settings/settings';
import { SyncService } from './../services/sync/sync.service';
import { FeedPessoalProvider } from './../providers/feed-pessoal/feed-pessoal';
import { AuthProvider } from './../providers/auth/auth';
import { Component, ViewChild } from '@angular/core';
import { Platform, Nav, MenuController, Events, ModalController } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { ToastController } from 'ionic-angular/components/toast/toast-controller';

@Component({
  templateUrl: 'app.html',
  providers: [ FeedPessoalProvider ]
})
export class MyApp {
  rootPage:any = 'FeedPage';
  @ViewChild(Nav) nav: Nav;

  /**
   * Usuario logado no app
   * 
   */
  public user = null;

  /**
   * Paginas do menu
   * 
   */
  public pages = [];

  /**
   * Feed pessoal
   * 
   */
  public feedPessoal = [];

  /**
   * Index do feed pessoal
   * 
   */
  public indexFeedPessoal = 5;

  /**
   * Método construtor
   * 
   * @param platform 
   * @param statusBar 
   * @param auth 
   * @param events 
   * @param feedPessoalProvider 
   * @param splashScreen 
   * @param menu 
   */
  constructor(platform: Platform, 
              statusBar: StatusBar,
              public auth: AuthProvider,
              public events: Events,
              public syncService: SyncService,
              public toastCtrl: ToastController,
              public feedPessoalProvider: FeedPessoalProvider,
              splashScreen: SplashScreen,
              public menu: MenuController,
              public modalCtrl: ModalController,
              public settingsProvider: SettingsProvider ) {

    // Quando a plataforma estiver pronta
    platform.ready().then(() => {
      statusBar.styleDefault();
      splashScreen.hide();
      this.setPages();
      this.getImageUser();
    });

    // Verifica se existem atualizações
    this.syncService.checkForUpdates()
    .then( ts =>  {

      // Verifica se existe um timestamp
      if ( ts ) {
        this.syncService.saveUpdatesIntoStorage();
      }
    });

    // Seta a autenticacao
    this.user = this.auth.user();
    if( this.user ) this.__getFeedPessoal();

    // Quando existe logout
    events.subscribe( 'logout', () => {
      this.menu.close();
      this.setPages();
      this.user = null;
      this.nav.setRoot( 'FeedPage' );
      this.feedPessoal = [];
    });

    // Toda vez que houver um follow novo
    events.subscribe( 'atualizar', () => {
      if( this.user ) this.__getFeedPessoal();
    });

    // Atualiza o menu ao logar
    events.subscribe( 'login', () => {
      this.user = this.auth.user();
      this.setPages();
      this.getImageUser();
      this.__getFeedPessoal();
    });

    // Atualiza o usuario
    events.subscribe( 'profile', () => {
      this.user = this.auth.user();
      this.getImageUser();
    });

    // Verifica se é a primeira vez
    if ( !localStorage.getItem( 'already-opened' ) ) {
      this.rootPage = "FiltrarModalPage";
    }  
  }

  /**
   * Busca a imagem do usuario
   */
  getImageUser() {
    if ( !this.user ) return;
    this.auth.getUserImage()
    .then( image => this.user.image = image )
    .catch( err => console.log(err) );
  }

  /**
   * Busca o feed pessoal
   */
  __getFeedPessoal() {
    this.feedPessoalProvider.get()
    .then( feed => {

      if( feed.length > 0 ) {
        
        // Seta a propriedade para abrir
        feed = feed.map( item => {
          item.open = false;
          return item;
        })
      
        // Seta od feeds
        this.feedPessoal = feed;
        
      } else {
        this.feedPessoal = [];
      }
      
    })
    .catch( err => { 
      console.log(err);
    });
  }

  /**
   * Mostra mais ou menos feed
   * 
   * @param length 
   * @param index 
   * @param feed 
   */
  toggleContent( length, index, feed ) {
    if ( index == length ) {
        this.indexFeedPessoal = 5;
    } else this.indexFeedPessoal = length;
  }

  /**
   * Abre e fecha as categorias do menu
   * @param index 
   * @param status 
   */
  openCategory( item ) {
    if( this.feedPessoal ) 
      item.open = !item.open;
  }

  /**
   * Seta as paginas do menu
   * 
   */
  setPages() {
    // paginas
    this.pages = [
      { 
        title: 'Entrar',               
        icon: 'log-in',            
        component: 'LoginPage', 
        appear: !this.auth.user() 
      }, { 
        title: 'Ler depois',             
        icon: 'bookmark',               
        component: 'NoticiasSalvasPage', 
        appear: this.auth.user() 
      }, { 
        title: 'Termos',             
        icon: 'information-circle', 
        component: 'TermosPage', 
        appear: false
      }, { 
        title: 'Suporte e Sugestão', 
        icon: 'call',               
        component: 'SuportePage', 
        appear: true  
      }
    ];
  }

  /**
   * openPage
   * 
   * @param page 
   */
  openPage(page) {

    // close the menu when clicking a link from the menu
    this.menu.close();

    // navigate to the new page if it is not the current page
    this.nav.setRoot( page.component );
  }

  /**
   * Faz o logout do sistema
   * 
   */
  doLogout() {
    this.auth.logout();
  }

  /**
   * perfilUsuario
   */
  perfilUsuario() {
    this.menu.close();
    this.nav.setRoot( 'PerfilUsuarioPage' );
  }

  /**
   * explorar
   */
  explorar() {
    this.menu.close();
    this.nav.setRoot( 'ExplorarPage' );
  }

  /**
   * Abre a edição de feed pessoal
   * 
   */
  editContent() {
    const modal = this.modalCtrl.create( 'EditarCategoriaPessoalPage' );
    modal.present();
  }

  /**
   * Abre o perfil do gateway
   */
  openGateway( gateway_id ) {
    this.nav.push( 'PerfilNoticiaPage', { id: gateway_id } );
    this.menu.close();
  }

  /**
   * Seta a categoria no local storage
   * 
   * @param feedType 
   * @param id 
   */
  setFeed( item, feed = false ) {
    
    // Redireciona pro feed padrao
    if( feed ) {
      localStorage.removeItem( 'feed-id' );
      this.nav.setRoot( 'FeedPage' );
      this.menu.close();
      return;
    }

    // Verifica qual o tipo da categoria
    if( item.categoria.personal_category ) {
      localStorage.setItem( 'feed-id', 'p-'+item.categoria.id );
    } else localStorage.setItem( 'feed-id', item.categoria.id );

    this.nav.setRoot( 'FeedPage', {name:item.categoria.name} );
    this.menu.close();
  }
}

