import { PipesModule } from './../pipes/pipes.module';
import { SettingsProvider } from './../providers/settings/settings';
import { DirectivesModule } from './../directives/directives.module';
import { RegionProvider } from './../providers/region/region';

// Services
import { SyncService } from './../services/sync/sync.service';

// Providers
import { AuthProvider } from './../providers/auth/auth';
import { ApiProvider } from './../providers/api/api';
import { ValidateProvider } from './../providers/validate/validate';
import { NoticeProvider } from '../providers/notice/notice';
import { GatewayProvider } from '../providers/gateway/gateway';
import { PersonalCategoryProvider } from '../providers/personal-category/personal-category';
import { FeedPessoalProvider } from '../providers/feed-pessoal/feed-pessoal';

// Modules
import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule, Events } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';
import { InAppBrowser } from '@ionic-native/in-app-browser';
import { MyApp } from './app.component';
import { HttpModule } from '@angular/http';
import { SafariViewController } from '@ionic-native/safari-view-controller';

// Plugins
import { SocialSharing } from '@ionic-native/social-sharing';
import { Camera } from '@ionic-native/camera';
import { CategoryProvider } from '../providers/category/category';

@NgModule({
  declarations: [
    MyApp
  ],
  imports: [
    BrowserModule,
    HttpModule,
    DirectivesModule,
    PipesModule,
    IonicModule.forRoot( MyApp, {
      tabsHideOnSubPages: true,
    }),
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    Camera,
    ValidateProvider,
    ApiProvider,
    AuthProvider,
    NoticeProvider,
    InAppBrowser,
    SocialSharing,
    GatewayProvider,
    PersonalCategoryProvider,
    FeedPessoalProvider,
    SyncService,
    Events,
    CategoryProvider,
    SettingsProvider,
    RegionProvider,
    SafariViewController,
  ]
})
export class AppModule {}



