import { Events } from 'ionic-angular';
import { ApiProvider } from './../api/api';
import { Injectable } from '@angular/core';

@Injectable()
export class SettingsProvider {

  constructor(public api: ApiProvider,
              public events: Events) {
    
    // Busca a tradução
    this.events.subscribe( 'translate', ( regions ) => {
      this.translate( regions )
      .then( retorno => localStorage.setItem( 'translate', JSON.stringify( retorno ) ) )
      .catch( err => err )
    });
  }

  /**
   * Pega os termos
   * 
   */
  public getTerms( key ) {    

    // Retorna uma promessa
    return this.api.get( `/settings/get_terms/${key}` )
    .then( terms => terms )
    .catch( err => err );
  }

  /**
   * Busca a tradução 
   * 
   * @param regions 
   */
  public translate( regions ) {
    return this.api.post( '/settings/get_category', { regions : regions } )
    .then( translate => translate )
    .catch( err => err );
  }
}

