import { ApiProvider } from './../api/api';
import { Injectable } from '@angular/core';

@Injectable()
export class PersonalCategoryProvider {

  constructor( public api: ApiProvider ) {}

  /**
   * saveCategory
   * 
   * faz o cadastro da categoria
   * 
   * @param dados 
   */
  public saveCategory( dados ) {
    
    // retorna a promessa
    return new Promise( ( resolve, reject ) => {

      this.api.post( '/personal_category/save_category', { name    : dados['name'], 
                                                          user_id : dados['user_id'] })
      .then( success => {
        
        // verifica se salvou
        if( success ) {
          resolve( success );
        } else {
          reject( 'Erro ao salvar no localStorage' );
        }
      }).catch( err => reject( err ) );     
    });
  }

  /**
   * Lista as categorias pessoais
   * 
   */
  public list() {
    return this.api.get( '/personal_category/list' )
    .then( cats => cats )
    .catch( err => err );
  }

  /**
   * Deleta uma categoria pessoal
   * 
   * @param personal_category_id 
   */
  public delete( personal_category_id ) {
    return this.api.get( `/personal_category/delete/${personal_category_id}` )
    .then( success => success )
    .catch( err => err );
  }
}

