import { CategoryProvider } from './../../providers/category/category';
import { RegionProvider } from './../../providers/region/region';
import { Injectable } from '@angular/core';
import { ApiProvider } from "../../providers/api/api";

@Injectable()
export class SyncService {

    /**
     * Método construtor
     * 
     */
    constructor( public api: ApiProvider,
                 public categoryProvider: CategoryProvider,
                 public regionProvider: RegionProvider ) {}

    /**
     * Pega a quantidade de publicidade
     * 
     */
    async get_publicity( slug ) {
        const publicity = await this.api.get( `/settings/get_by_slug/${slug}` );
        return publicity;
    }

    /**
     * Verifica se existe alguma atualização a ser baixada do servidor
     * 
     */
    async checkForUpdates(): Promise<Boolean> {
        
        // Verifica se existe um timestamp gravado
        const timestamp = localStorage.getItem( 'synced-timestamp' );
        if ( !timestamp ) return true;

        // Pega o timestamp
        const ts = await this.api.get( '/sync/get_date_update' );

        // Seta a quantidade de publicidade
        const publicity = await this.get_publicity( 'publicidade' );
        if( publicity ) {
            localStorage.setItem( 'publicity', JSON.stringify( publicity ) );
        }
        
        // Verifica se existem atualizações ou não
        return ( ts > timestamp ) ? true : false;
    }

    /**
     * Salva as atualizações no localstorage
     * 
     */
    async saveUpdatesIntoStorage(): Promise<Boolean> {

        // Pega os dados para atualizar
        const updates = await this.api.get( '/sync/get_data_to_sync' );
        
        // Verifica se recebeu os itens
        if ( updates['regions'] )    this.regionProvider.saveOnStorage( updates['regions'] );
        if ( updates['categories'] ) this.categoryProvider.updateDate( updates['categories'] );
        if ( updates['timestamp'] )  localStorage.setItem( 'synced-timestamp', updates['timestamp'] );

        // Volta true por padrão
        return true;
    }
}

