import { Events } from 'ionic-angular';
import { Category } from './category.model';
import { ApiProvider } from './../api/api';
import { Injectable } from '@angular/core';

@Injectable()
export class CategoryProvider {

  /**
   * Método construtor
   * 
   * @param api 
   */
  constructor( public api: ApiProvider, public events: Events ) {}

  /**
   * Faz as listagem das categorias buscando na api
   * 
   */
  listApi(): Promise<Category[]> {
    
    // Faz a requisicao
    return this.api.get( '/category/list')
    .then( ( res ) => {

      // Verifica se existe uma respose
      if ( res ) {
        return res as Category[];
      } else return [];
    })
    .catch( err => err );
  }

  /**
   * Verifica se a lista deve ser atualizada
   * 
   */
  async shouldUpdate(): Promise<boolean> {

    // Verifica se existe uma data salva
    const savedUpdatedDate = localStorage.getItem( 'updated-date' );
    
    // Pega a ultima data de atualizacao da api
    try {
      const lastUpdatedDate = await this.api.get( '/category/last_update' );

      // Verifica se já existe uma data setada
      if ( savedUpdatedDate ) {

        // Verifica se a data esta diferente
        if ( savedUpdatedDate != lastUpdatedDate ) {
          
          // seta no local storage 
          localStorage.setItem( 'updated-date', lastUpdatedDate );
          return true;

        } else return false;

      } else {
      
        // seta no local storage 
        localStorage.setItem( 'updated-date', lastUpdatedDate );
        return true;
      }

    } catch ( error ) {
      return true;
    }
  }

  /**
   * Pega as categorias do localstorage
   * 
   */
  listFromStorage(): Category[] {

    // Pega as categorias
    const categories = localStorage.getItem( 'categories' );
    if ( categories ) {
      
      // Transforma os dados em json
      let catJson = JSON.parse( categories );
      catJson = catJson.map( item => {
        if ( typeof item.order == 'undefined' ) item.order = 0;
        return item;
      });

      // Retorna o valor encontrado
      return catJson as Category[];
    } return [];
  }

  /**
   * Salva as categorias
   * 
   * @param categories 
   */
  saveCategoriesOnStore( categories: Category[], update = false ) {

    // Verifica se já existem categorias salvas no storage
    const cats = localStorage.getItem( 'categories' ); 

    // Verifica se é uma atualização
    if ( update && cats ) {

      // Seta as configurações do usuario
      let catJson = JSON.parse( cats );
      for( let index in categories ) {
        if ( catJson[index] ) {
          categories[index].showInAll = catJson[index].showInAll;
          categories[index].actived   = catJson[index].actived;
        }
      }
    } else {

      // Verifica se existe o index
      categories = categories.map( ( item ) => {
        item.showInAll = item.showInAll ? item.showInAll : false;
        item.actived   = item.actived   ? item.actived   : false;
        item.actived   = !cats ? false : item.actived;
        return item;
      });
    }

    // Salve no localstorage
    localStorage.setItem( 'categories', JSON.stringify( categories ) );
    this.events.publish( 'update:categories' );
  }

  /**
   * Lista as categorias ativas
   * 
   */
  listActived(): Category[] {

    let categories = this.listFromStorage();

    categories = categories.filter( item => {
      return item.actived;
    })

    // Arruma o array de acordo com a ordem salva
    categories.sort( ( a, b ) => {
      if ( !a.order ) a.order = "0";
      if ( !b.order ) b.order = "0";
      return parseInt( a.order.toString() ) - parseInt( b.order.toString() );
    });

    return categories;
  }

  /**
   * Categorias que estão no item TODOS
   * 
   */
  listInAll(): Category[] {
    let categories = this.listFromStorage();

    categories = categories.filter( item => {
      return item.showInAll;
    })

    // Arruma o array de acordo com a ordem salva
    categories.sort( ( a, b ) => {
      if ( !a.order ) a.order = "0";
      if ( !b.order ) b.order = "0";
      return parseInt( a.order.toString() ) - parseInt( b.order.toString() );
    });

    return categories;
  }

  /**
   * Faz o update dos registros
   * 
   */
  async updateDate( categoriesParam = null): Promise<any> {
    
    // Verifica se deve atualizar
    const shouldUpdate = await this.shouldUpdate();
    if ( shouldUpdate ) {

      // Pega os itens
      const categories       = categoriesParam ? categoriesParam : await this.listApi();
      const storedCategories = this.listFromStorage();

      // Seta o array com os ids
      const catIds       = categories.map( value => value.id );
      const catStoredIds = storedCategories.map( value => value.id );

      // Percorre as categorias recebida
      for( let index in catIds ) {

        // Verifica se já existe
        if ( catStoredIds.indexOf( catIds[index] ) !== -1 ) {
          
          // Pega o index atual
          let ind = catStoredIds.indexOf( catIds[index] );

          // Salva no array
          if ( categories[index] ) {
            categories[index].order = storedCategories[ind].order;
            storedCategories[ind]   = categories[index];
          }
        } else {
          if ( categories[index] )
            storedCategories.push( categories[index] );
        }
      }

      // Percorre as categorias armazenadas
      for( let index in catStoredIds ) {

        // Verifica se existem nos itens recebidos
        if ( catIds.indexOf( catStoredIds[index] ) === -1 ) {

          // Remove a categoria
          storedCategories.splice( parseInt( index ), -1 );
        }
      }

      // Guarda no localhost
      this.saveCategoriesOnStore( storedCategories, true );
      return true;
    } else return false;
  }
}

