import { Events } from 'ionic-angular';
import { Region } from './region.model';
import { Injectable } from '@angular/core';

@Injectable()
export class RegionProvider {

  constructor( public events: Events ) {}

  /**
   * Salva as regioes no localstorage
   * 
   * @param region
   */
  saveOnStorage( regions ) {
    if ( !localStorage.getItem( 'synced-regions' ) ) {
      for ( let r in regions ) {
        if ( regions[r].sigla.indexOf( 'br' ) !== -1 ) {
          regions[r].actived = true;
        } else {
          regions[r].actived = false;
        }
      }
    }
    localStorage.setItem( 'synced-regions', JSON.stringify( regions ) );

    // Dispara evento para tradução
    this.events.publish( 'translate', regions );
  }

  /**
   * Pega a listagem das regioes salvas
   * 
   */
  listFromStorage(): Region[] {

    // Pega no localstorage
    const regions = localStorage.getItem( 'synced-regions' );

    // Volta a listagem
    return regions ? JSON.parse( regions ) as Region[] : [];
  }

  /**
   * Seta a regiao ativa
   * 
   * @param sigla 
   */
  setActivedRegion( sigla ) {

    // Pega todos os itens
    const reg = this.listFromStorage();
    if ( !reg ) return null;

    for ( let r in reg ) {

      // Verifica se foi o selecionado
      if ( reg[r].sigla == sigla ) {
        reg[r].actived = true;
      } else {
        reg[r].actived = false;
      }
    }

    // Salva no storage
    this.saveOnStorage( reg );
  }

  /**
   * Pega a regiao ativa
   * 
   */
  getActivedRegion(): Region {
    
    // Pega todas as regioes
    const reg = this.listFromStorage();
    if ( !reg ) return null;

    // Verifica se existe algum ativo
    for ( let r in reg ) {
      if ( reg[r].actived ) return reg[r];
    }

    // Retorna o brasil como ativo por padrao
    for ( let r in reg ) {
      if ( reg[r].sigla.indexOf( 'BRL' ) !== -1 ) {
        reg[r].actived = true;
        return reg[r];
      } 
    }
  }
}

